import React from "react";
import * as Markdown from "react-markdown";
import { BrowserRouter, Route, Switch, Link } from "react-router-dom";

const BlogItem = (props,{match}) => {
    console.log(props)  
    return (
    <div className="content">
      <h1 className="content--main__heading">{props.match.params.id === props.path ? props.title : undefined}</h1>
      <h2 className="content--main__date">{props.match.params.id === props.path ? props.date : undefined}</h2>
      <Markdown className="content--main__paragraph" source={props.match.params.id === props.path ? props.content : undefined} />
    </div>
  );
};

export default BlogItem;



